.introduction {
  width: 100%;
	margin-bottom: 50px;
	border-width: 1px; 
	border-color: #e2e4e8;
	border-style: solid;
	border-radius: 5px;
	box-sizing: border-box;
}

.introduction .introduction-header {
	font-family: Consolas,monaco,monospace; 
	font-weight: normal;
	font-size: 14px;
	width: 100%;
	margin-top: 20px;
	margin-left: 20px;
}

.introduction .introduction-header .md-file {
	color: #6b737d;
}

.introduction .introduction-body {
	font-family: circular-std-book, Arial, sans-serif;
	margin-left: 20px;
	margin-right: 20px;
	font-size: 1em;
}

.introduction .introduction-body .wave-hand {
	-webkit-animation-name: wave-hand-animation;
	        animation-name: wave-hand-animation;
	-webkit-animation-duration: 2.5s;
	        animation-duration: 2.5s;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	transform-origin: 70% 70%;
	display: inline-block;
}

@-webkit-keyframes wave-hand-animation {
	0% { transform: rotate( 0.0deg) }
  10% { transform: rotate(14.0deg) }
	20% { transform: rotate(-8.0deg) }
  30% { transform: rotate(14.0deg) }
  40% { transform: rotate(-4.0deg) }
  50% { transform: rotate(10.0deg) }
  60% { transform: rotate( 0.0deg) }  
  100% { transform: rotate( 0.0deg) }
}

@keyframes wave-hand-animation {
	0% { transform: rotate( 0.0deg) }
  10% { transform: rotate(14.0deg) }
	20% { transform: rotate(-8.0deg) }
  30% { transform: rotate(14.0deg) }
  40% { transform: rotate(-4.0deg) }
  50% { transform: rotate(10.0deg) }
  60% { transform: rotate( 0.0deg) }  
  100% { transform: rotate( 0.0deg) }
}

.introduction .introduction-skills {
	font-family: Consolas,monaco,monospace;
	font-weight: normal;
	font-size: 14px;
	margin-left: 20px;
	width: 100%;
}

.introduction .introduction-body .introduction-interests {
	font-family: Consolas,monaco,monospace;
	font-weight: normal;
	font-size: 14px;
	width: 100%;
}

.introduction .introduction-conclusion {
	font-family: circular-std-book, Arial, sans-serif;
	margin-left: 20px;
	margin-right: 20px;
	font-size: 1em;
	margin-bottom: 20px;
}

.introduction .introduction-resume {
	font-family: circular-std-medium, Arial, sans-serif;
	margin-left: 20px;
	margin-right: 20px;
	font-size: 1.25em;
	margin-bottom: 20px;
}

.introduction .introduction-resume .introduction-fi-arrow-right {
	color: #05cf5b;
	align-items: center;
	margin-left: 5px;
	vertical-align: middle;
}

.introduction .introduction-resume:hover .introduction-fi-arrow-right {
	cursor: pointer;
	margin-left: 10px;
	transition: .2s ease-in-out;
}


